import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { FormGroup } from '@angular/forms';

@Component({
  selector: 'tm-counter-in-lists',
  templateUrl: './counter-in-lists.component.html',
  styleUrls: ['./counter-in-lists.component.css']
})
export class CounterInListsComponent implements OnInit {
  @Input('selected') items: any[] = [];
  @Input('item') item: any;
  constructor() { }

  ngOnInit() {
  }

  getCountSelectedItems() : Number {
    let result = 0;
    this.items.forEach((found) => {
      if(found._id === this.item._id) result++;
    });

    return result;
  }

  isSelect() {
    return !!this.items.find((found) => {
      if(found._id === this.item._id) return true;
    });
  }

}



