import { Component, OnInit, EventEmitter } from '@angular/core';
import { Router } from '@angular/router';
import { Observable } from 'rxjs/Observable';

import { Service, IServiceList } from "../service";
import { ToolsService } from '../../shared/tools.service';
import { PaginationService } from '../../shared/pagination.service';
import { ServicesService } from '../services.service';

import { MaterializeAction } from 'angular2-materialize/src';

@Component({
  selector: 'app-services',
  templateUrl: './services.component.html',
  styleUrls: ['./services.component.css']
})
export class ServicesComponent implements OnInit {
  public services$: Observable<Service[]>;
  public currentService: Service;

  constructor(
    private _tools: ToolsService,
    private _services: ServicesService,
    private _router: Router,
    private _pagination: PaginationService
    ) {}

  ngOnInit() {
    this.initServices();

    this._tools.getToolbar().change({
      title: "Список услуг",
      name: "services",
      actions: {
        add: () => this._router.navigate(['services/add'])
      },
      actionsPosition: {
        extend: ['add']
      }
    });
  }

  initServices() {
    this._pagination.setParams({
      nPerPage: 20
    }, 'services-list');
    this.reloadServices();
  }

  reloadServices(params?: any) {
    params = params !== undefined ? params : this._pagination.getParams('services-list');
    this.services$ = this._services.get(params)
      .map((result) => {
          this._pagination.setParams({
            countItems: result.count
          }, 'services-list');
          this._pagination.getPager('services-list');
        return result;
      })
      .map((result) => result.items.map(item => new Service(item)));
    this.services$.subscribe();
  }

  onEdit(item: Service) {
    this._router.navigate(['services/update/', item._id]);
  }

  deleteItem(item) {
    let self = this;

    let remove$ = this._services.remove(item._id);
    
    remove$.subscribe((data: any) => {
        if(data.status == 'ok') {
          this.reloadServices();
          this._tools.getToast().toast("Услуга удалена");
        }
      });
  }

  onSearchServices(text) {
    this._pagination.setParams({
      search: text,
      currentPage: 1
    }, 'services-list');

    this.reloadServices();
  }

  changePageServices(params) {
    this.reloadServices(params);
  }

}



